
body{
    background-color: #ece7e7;
}
.container{
    display: flex;
    justify-content: center;
    margin: 5% 0em 0em 0em;
}
.contain{
    display: flex;
    .leftc{
        background-color:rgb(201, 225, 230);
    }
    .middlec{
        padding:0px 10px;
        background-color: #fff;
    }
    .rightc{
        padding:0px 20px;
        text-align: center;
        background-color:rgb(39, 77, 113);
    }
}
.contain li{
    list-style: none;
    margin:2em 2em;
    font-size: 1em;
    font-weight: bold;
    color:rgb(99, 96, 96)
}
.li:hover{
    cursor: pointer;
    color: blue;
    transform: scale(1.2);
}
nav{
    display: flex;
    align-items: center;
    justify-content: center;
    color:black;
}
.date{
    padding-top:20px;
    font-size: larger;
    font-weight: bold;
}
.input input{
    width:300px;
    height:30px;
    background-color: #ede9e9;
    border:none;
    border-radius: 20px;
    outline:none;
}
.input button{
margin-left: 10px;
border: none;
}
.input{
    margin:0em 6em;
}
.weathercontent{
    list-style-type:none;
}
.cityHead{
    font-size: larger;
}
.f1{
    display:flex;
    justify-content: space-around;
    gap:1em;
    color:rgb(58, 56, 56);
    .tempc{
        padding:1em 2em;
        background-color: #f7f2f2;
        font-size: .9em;
        font-weight:600;
        width: 12em;
    }
    .humidityc{
        padding:1em 2em;
        background-color: #f7f2f2;
        font-size: .9em;
        font-weight:600;
        width: 12em;
    }
}
.weathercontent span{
    font-size: 1.5em;
    font-weight: bold;
}
.weathercontent li{
    display: flex;
    gap:1em;
}
.country,.data{
    color:white;
}
.country h1{
   font-size: 1em;
}
.data{
    font-size: 3em;
    display:flex;
    gap:1em;
    .statemain{
        font-size:.7em;
        position: relative;
        top:1em;
        color:rgb(233, 229, 229);
    }
}
.forcasticon{
    position: relative;
    top:3em;
    left:-4em

}
hr{
    position:relative;
    bottom:2em;
    border-color: aquamarine;
}
.sunsetd h3{
 color: white;
}
.contents{
    display:flex;
    justify-content: center;
    border:2px solid rgb(78, 78, 169);
    color:white;
    margin-bottom: 1em;
    background-color:rgba(41, 100, 156, 0.976);
    border-radius: 10px;
}
.contents .Sunrisen,.Sunsetn{
    color:rgb(155, 148, 148);
    position: relative;
    left:-1em;
    font-weight: bold;
}
.sunrisei img{
    height: 2.4em;
    position: relative;
    right:1em;
    top:1.8em;
    border-radius: 20px;
}